img {
  width: 20em;
  border-radius: 10px;
  margin-bottom: 1em;
}

.Certificates {
  height: 100vh;
}

.certheading {
  font-family: "Raleway", sans-serif;
  font-size: 3rem;
  border-bottom: 3px solid #622aff;
  color: #622aff;
  text-align: center;
  margin-top: 5vh;
  margin-left: auto;
  margin-right: auto;
}

.certheading {
  width: 17rem;
  margin-bottom: 1em;
}

@media (max-width: 767px) {
  .certheading {
    font-size: 2rem;
    width: 11rem;
  }
  img {
    width: 19em;
  }
  .Certificates {
    height: 210vh;
  }
}
